import React from "react";
import chats from "../StaticData/chats";
import people from "../StaticData/people";

import "./Chats.css";

export default function Chats(props) {
  return (
    <div className="all-chat">
      {chats
        .filter((chat) => chat.id === "Batman")[0]
        .messages.map((abc) => {
          const person = people.filter((person) => person.id === abc.user)[0];
          return (
            <div
              key={abc.user}
              className="chat-box"
              id={person.id}
              onClick={props.onClick}
            >
              <img
                className="friend-image"
                alt="friend-img"
                src={person.image}
              />
              <div className="friend-info">
                <h5 className="friend-name">Name : {person.name}</h5>
                <p className="friend-msg">Message:  </p>
              </div>
            </div>
          );
        })}
    </div>
  );
}
