import React, { useState } from "react";
import Header from "./Header/Header";
import MenuBar from "./MenuBar/MenuBar";
import Chats from "./Chats/Chats";
import Messenger from "./Messenger/Messenger";

import "./App.css";

function App() {
  const [messengerBox, showMessengerBox] = useState(false);
  const [personId, setPersonId] = useState();
  function handleClick(event) {
    showMessengerBox((prevValue) => !prevValue);
    setPersonId(event.target.parentElement.parentElement.id);
  }
 

  return (
    <div className="App">
      {!messengerBox && (
        <React.Fragment>
          <Header />
          <MenuBar />
          <Chats onClick={handleClick} />
        </React.Fragment>
      )}
      {messengerBox && <Messenger onClick={handleClick} pid={personId} />}
    </div>
  );
}

export default App;
